:root{
  --white: #f9f9f9;
  --black: #36383F;
  --gray: #85888C;
} /* variables*/

body {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  overflow: hidden;
  font-family: 'Roboto', sans-serif;
}

.App {
  height: 100vh;
}

/* Menu Icon */
.hamb{
  cursor: pointer;
  float: right;
  padding: 40px 20px;
}/* Style label tag */

.hamb-line {
  background: #000;
  display: block;
  height: 2px;
  position: relative;
  width: 24px;

} /* Style span tag */

.hamb-line::before,
.hamb-line::after{
  background: var(--black);
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%;
}
.hamb-line::before{
  top: 5px;
}
.hamb-line::after{
  top: -5px;
}

.side-menu {
  display: none;
} /* Hide checkbox */

.side-menu:checked ~ nav{
  max-height: 100%;
}
.side-menu:checked ~ .hamb .hamb-line {
  background: transparent;
}
.side-menu:checked ~ .hamb .hamb-line::before {
  transform: rotate(-45deg);
  top:0;
}
.side-menu:checked ~ .hamb .hamb-line::after {
  transform: rotate(45deg);
  top:0;
}

.nav{
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: var(--gray);
  overflow: hidden;

}
.menu li{
  display: block;
  color: var(--black);
}
.menu li:hover{
  background-color: var(--gray);
}
.nav{
  max-height: 0;
  transition: max-height .5s ease-out;
}

@media (min-width: 768px) {
  .nav{
      max-height: none;
      top: 0;
      position: relative;
      float: right;
      width: fit-content;
      background-color: transparent;
  }
  .menu li{
      float: left;
  }
  .menu li:hover{
      background-color: transparent;
      color: var(--gray);
  }
  .hamb{
      display: none;
  }
}

header {
  margin: auto 10%;
  padding: .5rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 450px) {
  header {
    display: flex;
    justify-content: normal;
    align-items: normal;
  }
}

.header-logo,
.header-navbar {
  width: 50%;
}

@media (max-width: 450px) {
  .header-logo,
  .header-navbar {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .sidemenu {
    z-index: 5;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}

.header-logo img {
  width: 15%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

@media (max-width: 450px) {
  .header-logo img {
    width: 30%;
  }
}

.header-navbar ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.header-navbar ul li {
  margin: auto 1.5rem;
  cursor: pointer;
  transition: 0.3s cubic-bezier(0.23, 1, 0.320, 1);
}

.header-navbar ul li:hover {
  border-bottom: 2px solid rgb(16, 146, 42);
  color: rgb(16, 146, 42);
  transition: 0.3s cubic-bezier(0.23, 1, 0.320, 1);
}

section {
  margin: auto 10%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

@media (max-width: 450px) {
  section {
    margin-right: 15%;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
  }
}

section .icon {
  position: absolute;
  cursor: pointer;
  width: 100%;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 450px) {
  section .icon{
    height: 90%;
    right: 0;
    align-items: flex-end;
    flex-direction: column;
  }
}

section .icon img {
  width: 4%;
  height: 10%;
  margin: 0 .3rem;
  animation: animate-to-top 1.25s cubic-bezier(0.23, 1, 0.320, 1);
  transition: .3s cubic-bezier(0.23, 1, 0.320, 1);
}

section .icon img:hover {
  transform: translateY(-10px);
  transition: .3s cubic-bezier(0.23, 1, 0.320, 1);
}

@media (max-width: 450px) {
  section .icon img {
    width: 10%;
    height: 8%;
  }
  section .icon img:hover  {
    transform: translateX(-10px);
  }
}

section .icon img:nth-child(2){
  width: 5%;
  height: 12%;
  padding-top: 1rem;
  margin-left: 1rem;
  animation: animate-to-top 2.25s cubic-bezier(0.23, 1, 0.320, 1);
}

@media (max-width: 450px) {
  section .icon img:nth-child(2) {
    width: 12%;
    height: 8%;
  }
}

section .icon img:nth-child(3) {
  width: 4.5%;
  animation: animate-to-top 3.25s cubic-bezier(0.23, 1, 0.320, 1);
}

@media (max-width: 450px) {
  section .icon img:nth-child(3) {
    width: 10%;
    margin-top: .7rem;
  }
}

section .side {
  width: 50%;
  display: flex;
  justify-content: center;
  transition: .3s ease-in-out;
}

@media (max-width: 450px) {
  section .side {
    width: 100%;
    margin: 1rem 0;
  }
}

.left {
  margin-top: 7%;
  align-items: flex-start;
  flex-direction: column;
  transition: .3s ease-in-out;
}

.right {
  align-items: flex-end;
}

.side.left span {
  margin: .5rem 0;
  font-size: 40px;
}
.side.left span,
.side.left p {
  letter-spacing: 1px;
  line-height: 1.5;
}

.side.left p {
  color: gray;
}

.side.left button {
  cursor: pointer;
  padding: .7rem 1.5rem;
  background-color: rgb(31, 136, 31);
  color: #fff;
  border: none;
  border-radius: 18px;
  transition: .15s ease-in-out;
}

.side.left button:hover {
  background-color: rgb(37, 163, 37);
  transition: .3s ease-in-out;
}

.frapucino-purple,
.frapucino-green,
.frapucino-pink {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

@media (max-width: 450px) {
  .frapucino-purple,
  .frapucino-green,
  .frapucino-pink {
    justify-content: center;
  }
}

.frapucino-purple img,
.frapucino-green img,
.frapucino-pink img{
  width: 60%;
  animation: animate-to-left 1.25s cubic-bezier(0.215, 0.610, 0.355, 1);
}

@media (max-width: 450px) {
  .frapucino-purple img,
  .frapucino-green img,
  .frapucino-pink img {
    width: 50%;
  }
}

.circle {
  position: absolute;
  z-index: -1;
  bottom: -800px;
  right: -650px;
  width: 1300px;
  height: 1300px;
  border-radius: 50%;
  animation: opact 2.25s cubic-bezier(0.23, 1, 0.320, 1);
}

@media (max-width: 450px) {
  .circle {
    display: none;
  }
}

.purple {
  background-color: #cf3ba5;
}

.green {
  background-color: #32582e;
}

.pink {
  background-color: #f773d0;
}

@keyframes animate-to-left {
  0% {
    opacity: 0;
    transform: translateX(210px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes animate-to-top {
  0% {
    opacity: 0;
    transform: translateY(110px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes opact {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}